body {
  color: #333;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

.header {
  text-align: center;
  border-bottom: 2px solid #3498db;
  margin-bottom: 30px;
  padding-bottom: 20px;
}

h1 {
  color: #2c3e50;
  margin-bottom: 5px;
}

.subtitle {
  color: #3498db;
  font-size: 1.2em;
  font-weight: bold;
}

.contact-info {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 10px;
  display: flex;
}

.contact-item {
  align-items: center;
  display: flex;
}

.contact-item > a {
  color: inherit;
  text-decoration: none;
  transition: transform .2s;
}

.contact-item > a:hover {
  transform: translateY(-3px);
}

.section {
  margin-bottom: 25px;
}

h2 {
  color: #2c3e50;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
  font-size: 1.3em;
}

.skills-grid {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 15px;
  display: grid;
}

.company {
  justify-content: space-between;
  margin-bottom: 5px;
  display: flex;
}

.company-name {
  font-weight: bold;
}

.tech-list {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.tech-tag-core {
  background: #e0e0e0;
  border-radius: 3px;
  padding: 3px 8px;
  font-size: .9em;
}

.tech-tag-other {
  background: #f4f4f4;
  border-radius: 3px;
  padding: 3px 8px;
  font-size: .9em;
}

.highlight {
  background-color: #f8f9fa;
  border-radius: 5px;
  margin: 10px 0;
  padding: 15px;
}

.projects-grid {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 15px;
  display: grid;
}

.project-card {
  background: #f8f9fa;
  border-left: 3px solid #3498db;
  border-radius: 8px;
  padding: 15px;
  transition: transform .2s;
}

.project-card:hover {
  transform: translateY(-3px);
}

.project-card h3 {
  color: #2c3e50;
  margin-top: 0;
  margin-bottom: 10px;
}

.project-card p {
  margin-bottom: 15px;
  font-size: .95em;
}

.project-link {
  color: #3498db;
  font-size: .9em;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.project-link:hover {
  text-decoration: underline;
}
